.explore-menu h1
{
    color: #262626;
    font-weight: 700;
    margin-bottom: 15px;
}
.explore-menu-text
{
    max-width: 60%;
    columns: #808080;
}
.explore-menu-list
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 20px 0px;
    overflow-x: scroll;
}
.explore-menu-list::-webkit-scrollbar{
    display: none;
}
.explore-menu-list img
{
    width: 7.5vw;
    min-width:80px;
    cursor: pointer;
    border-radius: 50%;
    transition: 0.2s;
}
.explore-menu-list p
{
    margin-top: 10px;
    color: #747474;
    font-size: max(1.4vw,16px);
    cursor: pointer;
}
.explore-menu hr
{
    margin: 10px 0px;
    height: 2px;
    background-color: #e2e2e2;
    border: none;
}
.explore-menu-list-item .active
{
    border:5px solid orangered;
    padding: 2px;
}